import React from 'react';
import { Link, graphql } from 'gatsby';
import posed from 'react-pose';
import styled from 'styled-components';
import { Row, Col, Button } from 'react-bootstrap';
import SEO from '../components/seo';
import { Layout } from '../components/layout';

const PosedSection = styled.div`
  background: #fff;
  color: var(--primary);
`;

const StyledHeader = styled.div`
  border-bottom-right-radius: 200px;
  position: relative;
  z-index: 1;
  padding: 2rem;
  padding-top: 150px;
  background: var(--primary);
  color: #fff;
`;

const StyledContent = styled.div`
  padding: 4rem 2rem;
`;
const Title = posed.h1({
  enter: { y: 0, opacity: 1 },
  exit: { y: 50, opacity: 0 }
});

const StyledTitle = styled(Title)`
  background: linear-gradient(to right, #f09819, #ff512f);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;
const P = posed.p({
  enter: { y: 0, opacity: 1 },
  exit: { y: 50, opacity: 0 }
});

const Navigation = styled(Row)`
  margin: 100px 0px 0px;
  text-align: center;
`;

const NavigationLink = styled(Link)`
  display: block;
  padding: 20px 0px 20px 10px;
`;
const BlogPostTemplate = ({ data, pageContext, location }) => {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark;

  const { previous, next } = pageContext;

  return (
    <Layout location={location}>
      <PosedSection initialPose="exit" pose="enter">
        <SEO title={frontmatter.title} description={data.excerpt} />

        <StyledHeader>
          <div className="container">
            <StyledTitle>{frontmatter.title}</StyledTitle>
            <P>{frontmatter.date}</P>
          </div>
        </StyledHeader>

        <div className="container">
          <StyledContent>
            <P dangerouslySetInnerHTML={{ __html: html }} />
          </StyledContent>

          <Navigation>
            <Col>
              {previous && (
                <NavigationLink to={previous.fields.slug} rel="prev">
                  ← {previous.frontmatter.title}
                </NavigationLink>
              )}
            </Col>
            <Col>
              {next && (
                <NavigationLink to={next.fields.slug} rel="next">
                  {next.frontmatter.title} →
                </NavigationLink>
              )}
            </Col>
          </Navigation>
        </div>
      </PosedSection>
    </Layout>
  );
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
      }
    }
  }
`;
